import React from 'react'
import { Link } from 'gatsby'
import styled from '@emotion/styled'
import Headroom from 'react-headroom'
import logo from '../../static/logo/dumna.svg'

const StyledLink = styled(Link)`
    position: fixed;
    display: inline-block;
    height: 200px;
    width: 200px;
    transition: all 0.25s ease-out
`

const Nav = styled.nav`
    position: relative;
    transform: translateY(45%);
    display: flex;
    justify-content: center;
    font-weight: 500;
    font-size: 1.25rem;
    align-items: center;
    a {
        color: ${props => props.theme.colors.black.base};
        margin-left: 2rem;
        transition: all ${props => props.theme.transitions};
        &:hover {
            color ${props => props.theme.colors.black.light};
        }
    }
`

const Logo = styled.img`
`

const Pinned = function() {
    //document.getElementById("logo").style.transform = "translateY(-20%)";
    document.getElementById("logo").style.opacity = "0";
    document.getElementById("nav").style.transform = "translateY(45%)";
};

const unPinned = function() {
    document.getElementById("logo").style.opacity = "1";
    document.getElementById("nav").style.transform = "translateY(0%)";
};
const unFixed = function() {
    document.getElementById("logo").style.opacity = "1";
    document.getElementById("nav").style.transform = "translateY(45%)";
};

const NavBar = () => (
    <Headroom disableInlineStyles hide pin instantSnap
    onPin={Pinned} onUnpin={unPinned} onUnfix={unFixed} 
    fixAnchor={270} pinAnchor={0} hideAnchor={1000} >
        <StyledLink id="logo" to="/">
            <Logo src={logo} alt='Dumna NiepodlegÅ‚a Logo' />
        </StyledLink>
        <Nav id="nav">
            <Link to='/'>Wydarzenia</Link>
            <Link to ='about'>O nas</Link>
            <Link to ='contact'>Kontakt</Link>
        </Nav>
    </Headroom>
);

export default NavBar